<template>
  <div class="person-manage">
    <Crumbs
      :parent-manage="'组织管理'"
      :children-manage="'责任人管理'"
      @isShowScreen="isShowScreen"
    ></Crumbs>

    <!-- 筛选内容 -->
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="screen"
        :label-width="62"
      >
        <Row
          type="flex"
          justify="space-between"
        >
          <Col span="7">
          <FormItem
            label="合同类别"
            width="100%"
          >
            <Select
              v-model="screen.contractType"
              filterable
            >
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          </Col>
          <Col span="7">
          <FormItem label="日期范围">
            <DatePicker
              v-model="screen.dataRange"
              type="daterange"
              placement="bottom-end"
              placeholder="Select date"
              style="width: 100%"
            ></DatePicker>
          </FormItem>
          </Col>
          <Col span="7">
          <FormItem
            label="乙方单位"
            width="100%"
          >
            <Select
              v-model="screen.partyUnit"
              filterable
            >
              <Option value="beijing">New York</Option>
              <Option value="shanghai">London</Option>
              <Option value="shenzhen">Sydney</Option>
            </Select>
          </FormItem>
          </Col>
        </Row>
        <Row
          type="flex"
          justify="end"
          style="margin-top:15px;"
        >
          <Col
            style="text-align: right; margin-right:8px;"
            span="1"
          >
          <Button
            type="primary"
            @click="handleScreen"
          >确认</Button>
          </Col>
        </Row>
      </Form>
    </Content>
    <!-- /筛选内容 -->

    <!-- table-content -->
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="handleAdd"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="handleModify"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
      >
        删除
      </Button>
      <Button
        icon="md-print"
        @click="handlePrint"
      >
        打印
      </Button>
      <Button
        icon="md-exit"
        @click="handleExport"
      >
        导出
      </Button>
      <Table
        ref="tableContent"
        highlight-row
        class="table"
        :columns="columns"
        :data="tableContent"
        style="margin-top: 15px;"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页组件 -->
      <Page
        style="text-align: right;margin-top: 15px;"
        :total="100"
        show-sizer
      />
      <!-- /分页组件 -->

      <!-- 弹出框 -->
      <Modal
        v-model="selTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled && !showTitle">请添加责任人信息</span>
          <span v-else>责任人信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="信息设置"
            name="name1"
          >
            <Form
              ref="formValidateType"
              :model="formValidateType"
              :rules="ruleValidateType"
              :label-width="80"
            >
              <Col span="24">
              <FormItem
                label="责任人说明"
                prop="personDis"
              >
                <Input
                  v-model.trim="formValidateType.personDis"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写责任人说明"
                ></Input>
              </FormItem>
              </Col>
              <Col span="24">
              <FormItem
                label="责任人内容"
                prop="personCon"
              >
                <Input
                  v-model.trim="formValidateType.personCon"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写责任人内容"
                ></Input>
              </FormItem>
              </Col>
              <Col span="24">
              <FormItem
                label="备注"
                prop="remarks"
              >
                <Input
                  v-model.trim="formValidateType.remarks"
                  :disabled="isDisabled"
                  type="textarea"
                  :autosize="{minRows: 1,maxRows: 5}"
                  placeholder="请填写备注"
                ></Input>
              </FormItem>
              </Col>
            </Form>
          </TabPane>
          <TabPane
            label="责任人管理"
            name="name3"
          >
            <Button
              icon="md-add"
              type="primary"
              @click="handleInlineAdd"
            >
              添加
            </Button>
            <Table
              ref="tableContent"
              highlight-row
              class="table"
              :columns="columnsInline"
              :data="tableInlineContent"
              style="margin-top: 15px;"
              @on-current-change="selectRow"
            ></Table>
            <!-- 分页组件 -->
            <Page
              style="text-align: right;margin-top: 15px;"
              :total="100"
              show-sizer
            />
          </TabPane>
          <TabPane
            label="附件"
            name="name7"
          >
            <Icon
              v-if="!isDisabled"
              size="16"
              type="md-alert"
            />
            <b v-if="!isDisabled">请上传与设置信息一致的附件或证明性文件，本系统只接受PDF格式文件，请上传前进行确认。</b>
            <Upload
              v-if="!isDisabled"
              action="//jsonplaceholder.typicode.com/posts/"
            >
              <Button
                class="uploadBtn"
                style="border: none;color: #2b85e4"
                icon="ios-cloud-upload-outline"
              >
                点击此处上传
              </Button>
            </Upload>
            <b
              v-else
              style="margin-bottom: 10px;display: inline-block;"
            >
              <Icon
                size="16"
                type="md-alert"
              />未上传相关附件</b>
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">打印</Button>
          <Button icon="md-exit">导出</Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="handleCancel('formValidateType')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="handleConfirm('formValidateType')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!-- /弹出框 -->

      <!-- 弹出框 -->
      <Modal
        v-model="selInlineTypeMod"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled && !showTitle">请添加责任人信息</span>
          <span v-else>责任人信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <Form
            ref="formInlineValidateType"
            :model="formInlineValidateType"
            :rules="ruleInlineValidateType"
            :label-width="80"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="姓名"
                prop="xingming"
              >
                <Input
                  v-model.trim="formInlineValidateType.xingming"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写姓名"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="小组职务"
                prop="zhiwu"
              >
                <Input
                  v-model.trim="formInlineValidateType.zhiwu"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写工作组职务"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="单位职务"
                prop="danwei"
              >
                <Input
                  v-model.trim="formInlineValidateType.danwei"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写所在单位及现任职务"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="联系电话"
                prop="dianhua"
              >
                <Input
                  v-model.trim="formInlineValidateType.dianhua"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写联系电话"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Col span="24">
            <FormItem
              label="责任分工"
              prop="fengong"
            >
              <Input
                v-model.trim="formInlineValidateType.fengong"
                :disabled="isDisabled"
                type="textarea"
                :autosize="{minRows: 1,maxRows: 5}"
                placeholder="请填写责任分工"
              ></Input>
            </FormItem>
            </Col>
            <Col span="24">
            <FormItem
              label="备注"
              prop="beizhu"
            >
              <Input
                v-model.trim="formInlineValidateType.beizhu"
                :disabled="isDisabled"
                type="textarea"
                :autosize="{minRows: 1,maxRows: 5}"
                placeholder="请填写备注"
              ></Input>
            </FormItem>
            </Col>
          </Form>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">打印</Button>
          <Button icon="md-exit">导出</Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="handleInlineCancel('formInlineValidateType')"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="handleInlineConfirm('formInlineValidateType')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!-- /弹出框 -->
    </Content>
    <!-- /table-content -->
  </div>
</template>

<script>
import Crumbs from '../components/Crumbs';

export default {
  name: 'PersonManage',
  components: { Crumbs },
  data() {
    return {
      // 设置是否选中行 默认为 false
      isSlectRow: false,
      // 控制筛选框显示隐藏  默认隐藏
      isScreen: false,
      // 默认表头
      showTitle: false,
      // 行内弹出框
      selInlineTypeMod: false,
      // 筛选内容_字段
      screen: {
        // 合同类别
        contractType: '',
        // 时间日期
        dataRange: '',
        // 乙方单位
        partyUnit: ''
      },
      // 表格内容 字段相对应
      tableContent: [
        {
          age: 1
        }
      ],
      tableInlineContent: [
        {
          age: 1
        }
      ],
      // 弹出框内容显示隐藏   默认隐藏
      selTypeMod: false,
      // 控制 是否禁用  默认不禁用
      isDisabled: false,
      // 是否将弹层放置于 body 内  设置为true
      transferValue: true,
      // 添加表单的内容字段  ->  传递数据的字段
      formValidateType: {
        personDis: '',
        personCon: '',
        remarks: ''
      },
      formInlineValidateType: {
        xingming: '',
        zhiwu: '',
        danwei: '',
        dianhua: '',
        fengong: '',
        beizhu: ''
      },
      // 表单内容的校验规则
      ruleValidateType: {
        personDis: [
          { required: true, message: '请填写责任人说明', trigger: 'blur' }
        ],
        personCon: [
          { required: true, message: '请填写责任人内容', trigger: 'blur' }
        ]
      },
      ruleInlineValidateType: {
        xingming: [
          { required: true, message: '请填写姓名', trigger: 'blur' }
        ],
        zhiwu: [
          { required: true, message: '请填写工作组职务', trigger: 'blur' }
        ],
        danwei: [
          { required: true, message: '请填写所在单位及现任职务', trigger: 'blur' }
        ],
        dianhua: [
          { required: true, message: '请填写联系电话', trigger: 'blur' }
        ],
        fengong: [
          { required: true, message: '请填写责任分工', trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    columnsInline() {
      return [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '职务',
          key: 'age'
        },
        {
          title: '姓名',
          key: 'age'
        },
        {
          title: '所在单位及现任职务',
          key: 'age'
        },
        {
          title: '责任分工',
          key: 'age'
        },
        {
          title: '联系电话',
          key: 'age'
        },
        // TODO: 分三种 正在审 未通过 已通过
        {
          title: '审核状态',
          key: 'age',
          fixed: 'right',
          align: 'center',
          width: '120'
        }
      ]
    },
    columns() {
      return [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '责任人说明',
          key: 'age'
        },
        {
          title: '责任人内容',
          key: 'age'
        },
        // TODO: 分三种 正在审 未通过 已通过
        {
          title: '审核状态',
          key: 'age',
          fixed: 'right',
          align: 'center',
          width: '120'
        }
      ]
    }
  },
  methods: {
    // 子组件接收 控制筛选内容的隐藏和显示
    isShowScreen(msg) {
      this.isScreen = msg;
    },
    // 筛选确认按钮
    handleScreen() { },
    // 添加批复 弹出框显示
    handleAdd() {
      // 禁用状态关闭
      this.isDisabled = false;
      // 弹出框显示
      this.selTypeMod = true;
    },
    handleInlineAdd() {
      this.selInlineTypeMod = true;
    },
    // 更新操作
    handleModify() { },
    // 打印操作
    handlePrint() { },
    // 导出操作
    handleExport() { },
    // 选中一行进行修改数据
    selectRow(currentRow, oldCurrentRow) {
      this.isSlectRow = true;
    },
    // 取消操作
    handleCancel(name) {
      this.$refs[name].resetFields();
      this.selTypeMod = false;
    },
    // 确认操作   关系后台数据
    handleConfirm(name) {
      this.$refs[name].validate((valid) => { })
    },
    handleInlineCancel(name) {
      this.$refs[name].resetFields();
      this.selInlineTypeMod = false;
    },
    handleInlineConfirm() {}
  }
}
</script>

<style lang="scss" scoped></style>



